@import "./shared.css";

img {
  max-width: 100%;
}

#root {
  max-width: 45em;
  width: 100%;
}

/* logo */
h1 {
  margin: 0;
}

/* tagline */
h1 + p {
  color: #52B2E4;
  font-size: 1.16em;
  max-width: 20em;
  margin: 2.25em auto;
}

h2 {
  color: #52B2E4;
  font-weight: normal;
  font-size: 1.33em;
  margin-top: 3em;
}

h3 {
  font-weight: normal;
  font-size: .77em;
  margin-bottom: .5em;
}

.header-anchor {
    float: right;
    transition: opacity ease-in-out 100ms;
    opacity: 0;
}

h1:hover > .header-anchor,
h2:hover > .header-anchor,
h3:hover > .header-anchor {
  opacity: 1;
}

@media (min-width: 920px) {
  .header-anchor {
    position: absolute;
    float: none;
    display: inline;
    padding-right: .7em;
    margin-left: -1.5em;
    transition: opacity ease-in-out 100ms;
    opacity: 0;
  }
}

center {
  margin: 3em 0;
}

pre {
  font-size: 1em;
  color: #7E7E7E;
  border: 1px solid #E5E5E5;
  border-radius: .6em;
  padding: 1em;
  margin: 0 -.2em;
  white-space: pre;
  white-space: pre-wrap;
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}

@media (min-width: 480px) {
  pre {
    padding: 2em;
  }
}

pre span.emphasis {
  color: #6A00C9;
}

a:link.github-link, a:visited.github-link {
  text-indent: 1em;
  background-color: #6A00C9;
  background-image: url(images/github-logo.svg);
  background-repeat: no-repeat;
  background-size: auto 66%;
  background-position: .5em .5em;
}

hr {
  max-width: 16em;
  border: none;
  border-top: 1px solid #E5E5E5;
  margin: 3em auto;
}

a:link, a:visited {
  color: #6A00C9;
  text-decoration: none;
}

a:link:hover, a:visited:hover {
  text-decoration: underline;
}

.repl-image {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 37.6744186%;
  background: url(images/repl-backdrop.png) 100%/100%;
}

.repl-image > div {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

a:link.bubble-link.white-bubble,
a:visited.bubble-link.white-bubble {
  color: #6A00C9;
  background-color: white;
}